// 等同于调用axios,调用的axios的实例
import request from '@/utils/request'

// 获取图形验证码
export const getPictureCode = function () {
  return request.get('/captcha/image')
}

// 获取验证码
export const getMmsgCode = (captchaCode, captchaKey, mobiel) => {
  return request.post('/captcha/sendSmsCaptcha', {
    form: {
      captchaCode,
      captchaKey,
      mobiel
    }
  })
}

// 验证登录
export const codeLogin = (mobile, smsCode) => {
  return request.post('/passport/login', {
    form: {
      isParty: false,
      partyData: {},
      mobile,
      smsCode
    }
  })
}
